<template>
  <div class="signout">
    <h2>Sign out</h2>
    <v-btn color="primary" @click="googleSignOut">Sign out from Google</v-btn>
  </div>
</template>
<script>
import firebase from "firebase";

export default {
  name: "Signout",
  methods: {
    googleSignOut: function() {
      firebase.auth().onAuthStateChanged(user => {
        let displayName = user.displayName;
        let email = user.email;
        firebase
          .auth()
          .signOut()
          .then(() => {
            alert(`${displayName}(${email})からログアウトしました`);
          })
          .catch(error => {
            alert(`ログアウト時にエラーが発生しました (${error})`);
          });
      });
    }
  }
};
</script>
